import { Box, Button, Typography } from "@material-ui/core";
import { Field, Form, Formik } from "formik";
import React, { memo, useState } from "react";
import { SimpleModal } from "../../../components/Modal";
import * as Yup from "yup";
import { InputPasswordComponent } from "../../../components";
import LogoComponent from "../../../resources/images/Logo";

function ResetPassword({ toggle, visible, t, classes, callbackSubmitChangePassword, request_id }) {
  const [isCountTime, setIsCountTime] = useState(false);

  const initialValues = {
    new_password: "",
    new_password_confirm: "",
    request_id,
  };

  const validationSchema = Yup.object().shape({
    new_password: Yup.string()
      .min(8, t("message.MSG_8"))
      .max(16, t("message.MSG_8"))
      .required(t("message.MSG_4", { fieldName: t("profile.label_new_pw") }))
      .matches(/^(?=.*[a-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,16}$/, t("message.MSG_8")),
    new_password_confirm: Yup.string().oneOf([Yup.ref("new_password"), null], t("message.MSG_9")),
  });

  const onSubmitChangePassword = (values) => {
    callbackSubmitChangePassword(values);
  };

  return (
    visible && (
      <Formik
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={onSubmitChangePassword}
        enableReinitialize
      >
        {({ handleSubmit, setFieldValue, values, isValid }) => (
          <Box className={classes.boxFormEditProfile}>
            <Form className="form">
              <LogoComponent className="center-margin-block mb-46" />
              <Typography variant="h5" className="center-block mb-16">
                {t("profile.title_reset_password")}
              </Typography>
              <Form className="mt-30 mb-12 full-width">
                <Field
                  label={t("profile.label_new_pw")}
                  maxLength={25}
                  placeholder={t("profile.text_enter_new_pw")}
                  name="new_password"
                  component={InputPasswordComponent}
                />
              </Form>
              <Form className="mt-30 mb-46 full-width">
                <Field
                  label={t("profile.label_confirm_new_pw")}
                  maxLength={25}
                  placeholder={t("profile.text_enter_password")}
                  name="new_password_confirm"
                  component={InputPasswordComponent}
                />
              </Form>
              <div className={classes.btnConfirmWrap}>
                <Button
                  className={`button button-transparent-border ${classes.btnCancelConfirm}`}
                  fullWidth
                  onClick={toggle}
                >
                  {t("component.btn_back")}
                </Button>
                <Button
                  disabled={!isValid}
                  type="submit"
                  className={`button button-gradient ${classes.btnNextConfirm}`}
                  onClick={handleSubmit}
                  fullWidth
                >
                  {t("component.btn_continue")}
                </Button>
              </div>
            </Form>
          </Box>
        )}
      </Formik>
    )
  );
}

export default memo(ResetPassword);
