import React from "react";
import { TextField, makeStyles } from "@material-ui/core";
import PropTypes from "prop-types";
import MuiPhoneNumber from "material-ui-phone-number";
import clsx from "clsx";

InputComponent.propTypes = {
  field: PropTypes.object,
  form: PropTypes.object,
  props: PropTypes.object,
};

InputComponent.defaultProps = {
  field: null,
  form: {
    errors: null,
    touched: null,
  },
  props: {},
};

const useStylesInput = makeStyles({
  input: {
    "& textarea": {
      wordBreak: "break-word",
    },
  },
});

function InputComponent({
  field,
  form: { errors, touched },
  message,
  inputValue,
  maxLength,
  inputProps,
  className = "",
  ...props
}) {
  const classes = useStylesInput();

  return field ? (
    <TextField
      {...field}
      {...props}
      error={Boolean(message) || (touched[field.name] && Boolean(errors[field.name]))}
      helperText={message || (touched[field.name] && errors[field.name])}
      variant="outlined"
      className={`${classes.input} ${className}`}
      inputProps={{
        ...inputProps,
        maxLength: maxLength,
      }}
    />
  ) : (
    <TextField {...props} error={Boolean(message)} helperText={message} variant="outlined" />
  );
}

export default InputComponent;

const useStylesInputPhoneNumber = makeStyles({
  phoneNumber: {
    "& .country-name": {
      display: "none",
    },
  },
  disabledInput: {
    pointerEvents: "none",
  },
});

export function InputPhoneNumber({
  field,
  form: { errors, touched },
  message,
  inputValue,
  maxLength,
  inputProps,
  regionPhoneNumber,
  isDisabled = false,
  ...props
}) {
  const classes = useStylesInputPhoneNumber();

  return (
    <MuiPhoneNumber
      {...field}
      {...props}
      error={Boolean(message) || (touched[field.name] && Boolean(errors[field.name]))}
      helperText={message || (touched[field.name] && errors[field.name])}
      variant="outlined"
      inputProps={{
        ...inputProps,
        maxLength: maxLength,
      }}
      countryCodeEditable={false}
      disabled={isDisabled}
      autoFormat={false}
      data-cy="user-phone"
      onlyCountries={["vn", "jp", "gb", "cn"]}
      dropdownClass={classes.phoneNumber}
      inputClass={clsx({
        [classes.disabledInput]: isDisabled,
      })}
    />
  );
}
