  right: {
    textAlign: "right",
  },
  ...themeConfigLink(theme),
  ...themeConfigButtonSave(theme, true),
  buttonSend: {
    padding: 10,
    marginRight: theme.spacing(2),
  },
  buttonCloseModalDetail: {
    margin: "auto",
    marginTop: theme.spacing(6),
    width: "274px",
  },
  boxModalKycHistory: {
    padding: theme.spacing(4),
    ...themConfigModalOverflow(),
  },
