import React, { Fragment, useCallback, useState } from "react";
import {
  Badge,
  Box,
  IconButton,
  makeStyles,
  List,
  ListItem,
  SwipeableDrawer,
  Typography,
  Button,
} from "@material-ui/core";
import { Notifications as NotificationsIcon, Close as CloseIcon } from "@material-ui/icons";
import clsx from "clsx";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import moment from "moment";
import { useHistory } from "react-router-dom";

import { NOTIFICATION_TYPES } from "../../../common/constant";
import { formatDate } from "../../../utils";
import { actions } from "../../../pages/layout/actions";

const useStyles = makeStyles((theme) => ({
  badge: {
    marginRight: theme.spacing(2),

    "& .MuiSvgIcon-root": {
      color: theme.overrides.colorWhite,
    },
    "& .MuiBadge-badge": {
      backgroundColor: theme.overrides.colorBurntSienna,
    },
  },
  top: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: theme.spacing(2),
  },
  content: {
    padding: theme.spacing(2, 0),
  },
  list: {
    width: 450,
    backgroundColor: theme.palette.background.paper,
  },
  listItem: {
    flexDirection: "column",
    alignItems: "start",
  },
  listItemUnRead: {
    backgroundColor: theme.overrides.colorConcrete,
  },
  time: {
    display: "flex",

    "& > .date": {
      marginRight: theme.spacing(1),
    },
  },
}));

function Notification(props) {
  const { notifications } = props;
  const [isVisible, setIsVisible] = useState(false);
  const classes = useStyles();
  const { t } = useTranslation();
  const dispatch = useDispatch();
  let history = useHistory();
  const userCode = useSelector((state) => state.userDetail.userDetail?.code);

  const handleOpen = useCallback(() => {
    setIsVisible((prevState) => !prevState);
  }, [isVisible]);

  const readAllNotification = () => {
    dispatch(actions.readNotificationStart(null));
  };

  const readNotification = (id, link) => () => {
    dispatch(actions.readNotificationStart([id]));
    history.push(link);
    handleOpen();
  };

  const renderTime = (date) => {
    return (
      <Box className={classes.time}>
        <Typography className="date" variant="body2">
          {formatDate(date)}
        </Typography>
        <Typography variant="body2">{moment(date).fromNow()}</Typography>
      </Box>
    );
  };

  const renderNotification = (notification) => {
    const { notification_type, sender_code, sender_name, created_at, read, id, data } = notification;

    switch (notification_type) {
      case NOTIFICATION_TYPES[0]:
        return (
          <ListItem
            className={clsx(classes.listItem, { [classes.listItemUnRead]: !read })}
            button
            onClick={readNotification(id, `/customers/${sender_code}`)}
          >
            <Typography variant="subtitle1">
              {t("component.notification_customer_update_kyc", { customerCode: sender_code })}
            </Typography>
            {renderTime(created_at)}
          </ListItem>
        );
      case NOTIFICATION_TYPES[1]:
        return (
          <ListItem
            button
            className={clsx(classes.listItem, { [classes.listItemUnRead]: !read })}
            onClick={readNotification(id, `/list-kyc/detail/${id}`)}
          >
            <Typography variant="subtitle1">
              {t("component.notification_customer_request_edit_kyc", {
                customerCode: sender_code,
                requestCode: sender_code,
              })}
            </Typography>
            {renderTime(created_at)}
          </ListItem>
        );
      case NOTIFICATION_TYPES[2]:
        return (
          <ListItem
            button
            className={clsx(classes.listItem, { [classes.listItemUnRead]: !read })}
            onClick={readNotification(id, `/customers/${sender_code}`)}
          >
            <Typography variant="subtitle1">
              {userCode === data?.new_pic_code &&
                t("component.notification_employee_received_pic", {
                  employeeName: sender_name,
                  customerCode: sender_code,
                })}
              {userCode === data?.old_pic_code &&
                t("component.notification_employee_change_pic", {
                  employeeName: sender_name,
                  customerCode: sender_code,
                })}
            </Typography>
            {renderTime(created_at)}
          </ListItem>
        );

      default:
        break;
    }
  };

  return (
    <Box>
      <IconButton className={classes.badge} onClick={handleOpen}>
        <Badge color="secondary" variant="dot" invisible={notifications.every((notification) => notification.read)}>
          <NotificationsIcon />
        </Badge>
      </IconButton>
      <SwipeableDrawer anchor="right" open={isVisible} onClose={handleOpen} onOpen={handleOpen}>
        <Box className={classes.top}>
          <Typography variant="h6">{t("component.title_notification")}</Typography>
          <IconButton onClick={handleOpen}>
            <CloseIcon />
          </IconButton>
        </Box>
        <Button onClick={readAllNotification}>{t("component.text_read_all")}</Button>
        <List component="nav" className={classes.list} aria-label="contacts">
          {notifications.map((notification, index) => (
            <Fragment key={index}>{renderNotification(notification)}</Fragment>
          ))}
        </List>
      </SwipeableDrawer>
    </Box>
  );
}

export default Notification;
