//config colum
export const columns = (t) => {
  return [
    { dataIndex: "no", align: "center", label: t("customer_management.text_no"), isSorter: false },
    { dataIndex: "code", align: "left", label: t("list_kyc.label_request_id"), isSorter: true },
    { dataIndex: "created_at", align: "left", label: t("list_kyc.label_request_date"), isSorter: true },
    { dataIndex: "customer_name", align: "left", label: t("list_kyc.label_customer_name"), isSorter: true },
    { dataIndex: "department_names", align: "center", label: t("list_kyc.label_department"), isSorter: false },
    { dataIndex: "idoc_type", align: "center", label: t("list_kyc.label_identity_document"), isSorter: false },
    { dataIndex: "status", align: "center", label: t("list_kyc.label_request_status"), isSorter: false },
    { dataIndex: "action", align: "center", label: t("list_kyc.label_action"), isSorter: false },
  ];
};

//render data to table
export const renderDataKycRequest = (listKyc, filters, classes, t, callbackRequestKyc) => {
  return listKyc?.map((item, index) => {
    const { page, pageSize } = filters;
    return {
      no: index + 1 + page * pageSize,
      code: item.code,
      created_at: formatDate(item.created_at),
      customer_name: item.customer_name,
      department_names: (
        <>
          {item.department_names.map((item, index) => (
            <div key={index}>{item}</div>
          ))}
        </>
      ),
      idoc_type: DOCUMENT_TYPE_NAME[item.idoc_type],
      status: renderStatusKYC(classes, item?.status),
      action: (
        <Link className={classes.link} to="#" onClick={callbackRequestKyc(item)}>
          {t("customer_management.button_view_detail")}
        </Link>
      ),
    };
  });
};

//render kyc status DOM
export const renderStatusKYC = (classes, status) => {
  return (
    <span
      className={clsx(classes.status, {
        [classes.statusVerify]: status === "VERIFYING",
        [classes.statusApproved]: status === "APPROVED",
        [classes.statusRejected]: status === "REJECTED",
        [classes.statusRequesting]: status === "REQUESTING",
      })}
    >
      {status === "REQUESTING" ? "PENDING" : status}
    </span>
  );
};
