import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Paper from "@material-ui/core/Paper";
import ImportExportIcon from "@material-ui/icons/ImportExport";
import { useTranslation } from "react-i18next";

import NODATA from "../../resources/images/no_data.png";
import { Box, Typography } from "@material-ui/core";

function EnhancedTableHead(props) {
  const { classes, order, orderBy, headCells = [], onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead>
      <TableRow>
        {headCells.map((headCell) =>
          headCell.isSorter ? (
            <TableCell
              key={headCell.dataIndex}
              align={headCell.align || "left"}
              sortDirection={orderBy === headCell.id ? order : false}
              width={headCell.width || "unset"}
              className={classes.tableCell}
            >
              <TableSortLabel
                active={headCell.isSorter}
                direction={orderBy === headCell.dataIndex ? order : "asc"}
                onClick={createSortHandler(headCell.dataIndex)}
                IconComponent={ImportExportIcon}
              >
                <Typography variant="subtitle2">{headCell.label}</Typography>
              </TableSortLabel>
            </TableCell>
          ) : (
            <TableCell
              align={headCell.align || "left"}
              key={headCell.dataIndex}
              width={headCell.width || "unset"}
              className={classes.tableCell}
            >
              <Typography variant="subtitle2">{headCell.label}</Typography>
            </TableCell>
          )
        )}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  classes: PropTypes.object.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  headCells: PropTypes.arrayOf(
    PropTypes.shape({
      dataIndex: PropTypes.string.isRequired,
    })
  ).isRequired,
};

EnhancedTableHead.defaultProps = {
  classes: {},
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  paper: {
    width: "100%",
    marginBottom: theme.spacing(2),
  },
  tableContainer: {
    overflowX: "hidden",
  },
  table: {
    minWidth: 750,
  },
  tableCell: {
    wordBreak: "keep-all",
    padding: theme.spacing(1),
  },
  boxNoData: {
    textAlign: "center",
    padding: theme.spacing(4),
    opacity: 0.7,
    textTransform: "uppercase",
  },
}));

export default function EnhancedTable(props) {
  const classes = useStyles();
  const { t } = useTranslation();

  const {
    order,
    orderBy,
    headCells,
    rows,
    rowPerPage,
    rowPerPageOptions,
    handleFilters,
    rowLength,
    currentPage,
    onChangePage,
    onChangeRowsPerPage,
  } = props;

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === "asc";
    const newOrder = isAsc ? "desc" : "asc";
    handleFilters({
      order: newOrder,
      orderBy: property,
    });
  };

  const handleChangePage = (event, newPage) => {
    onChangePage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    onChangeRowsPerPage(parseInt(event.target.value, 10));
  };

  const emptyRows = rowPerPage - Math.min(rowPerPage, rowLength - currentPage * rowPerPage);

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <TableContainer className={classes.tableContainer}>
          <Table className={classes.table} aria-labelledby="tableTitle" size={"medium"} aria-label="enhanced table">
            <EnhancedTableHead
              classes={classes}
              order={order}
              orderBy={orderBy}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
              headCells={headCells}
            />
            <TableBody>
              {rows.map((row, index) => {
                const labelId = `enhanced-table-checkbox-${index}`;

                return (
                  <TableRow key={index} hover tabIndex={-1}>
                    {headCells.map((item, index) =>
                      index === 0 ? (
                        <TableCell
                          key={item.dataIndex}
                          component="th"
                          scope="row"
                          id={labelId}
                          align={item.align}
                          width={item.width || "unset"}
                          className={classes.tableCell}
                        >
                          {row[item.dataIndex] || "---"}
                        </TableCell>
                      ) : (
                        <TableCell
                          key={item.dataIndex}
                          align={row.align || item.align}
                          width={item.width || "unset"}
                          className={classes.tableCell}
                        >
                          {row[item.dataIndex] || "---"}
                        </TableCell>
                      )
                    )}
                  </TableRow>
                );
              })}
              {!rows.length && (
                <TableRow style={{ height: 53 * emptyRows }}>
                  <TableCell colSpan={12} style={{ borderBottom: "none" }}>
                    <Box className={classes.boxNoData}>
                      <img src={NODATA} alt="no data" />
                      <Typography variant="subtitle1">{t("common.no_data")}</Typography>
                    </Box>
                  </TableCell>
                </TableRow>
              )}
              {emptyRows > 0 && (
                <TableRow style={{ height: 53 * emptyRows }}>
                  <TableCell colSpan={12} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={rowPerPageOptions}
          component="div"
          count={rowLength || 0}
          rowsPerPage={rowPerPage}
          page={currentPage}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
    </div>
  );
}
