import { Box, Grid, Typography } from "@material-ui/core";
import React, { memo, useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import { renderStatusKYC, useStyles } from "./style";
import { actions } from "./actions";
import {
  DOCUMENT_TYPE_NAME,
  KEY_UPLOAD_HISTORY_DETAIL,
  KYC_INFO_IDENTITY_PAGE,
  KYC_INFO_PAGE,
  KYC_INFO_PAGE_STATUS,
} from "../../common/constant";
import { formatDate } from "../../utils";
import ZoomPhotoChildImage from "../../components/ZoomPhoto/childImage";

function KYCHistory(props) {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const classes = useStyles();
  const detailHistoryStore = useSelector((state) => state.detailKYCHistory.kycHistory);
  const detailHistory = {
    ...detailHistoryStore,
    idoc_type: DOCUMENT_TYPE_NAME[detailHistoryStore?.idoc_type],
    status: renderStatusKYC(classes, detailHistoryStore?.status),
    requested_at: formatDate(detailHistoryStore?.requested_at),
    submitted_at: formatDate(detailHistoryStore?.submitted_at),
    reviewed_at: formatDate(detailHistoryStore?.reviewed_at),
  };
  useEffect(() => {
    document.getElementsByTagName("main")[0].style = "padding-top: 26px";
    document.getElementById("root").style.minHeight = "100vh";
    document.getElementById("root").style.background = "#e8e8e8ab";
  });

  useEffect(() => {
    if (detailHistory?.full_name) {
      document.title = `${detailHistory.full_name} | ${t("customer_management.tab_kyc_edit_history")}`;
    }
  }, [detailHistory]);

  useEffect(() => {
    const { customerId, kycId } = props.match.params;
    dispatch(actions.getKYCHistoryStart(customerId, kycId));
  }, []);

  const renderInforWraper = (item) => {
    return (
      <Grid key={item.label} item md={6}>
        <Typography variant="subtitle1">{item.label}</Typography>
        <Typography variant="body2">{detailHistory[item.prop]}</Typography>
      </Grid>
    );
  };

  return (
    <Box className={classes.boxContainer}>
      <Grid container spacing={2}>
        <Grid item md={8}>
          <Typography className={classes.kycTitleContent} variant="h6">
            {t("customer_management.heading_contact_information")}
          </Typography>
          <Grid className={classes.gridInfoContainer} container spacing={2}>
            {KYC_INFO_PAGE.map(renderInforWraper)}
          </Grid>
          <Typography className={classes.kycTitleContent} variant="h6">
            {t("list_kyc.label_identity_document")}
          </Typography>
          <Grid className={classes.gridInfoContainer} container spacing={2}>
            {KYC_INFO_IDENTITY_PAGE.map(renderInforWraper)}
          </Grid>
          <Typography className={classes.kycTitleContent} variant="h6">
            {t("customer_management.heading_status_history")}
          </Typography>
          <Grid className={classes.gridInfoContainer} container spacing={2}>
            {KYC_INFO_PAGE_STATUS.map(renderInforWraper)}
          </Grid>
        </Grid>
        <Grid item md={4} className="">
          <Typography className={classes.kycTitleContent} variant="h6">
            {t("customer_management.heading_kyc_photos")}
          </Typography>
          {KEY_UPLOAD_HISTORY_DETAIL.map((item) =>
            detailHistory[`${item}_url`] ? (
              <Grid key={item} item md={12}>
                <ZoomPhotoChildImage fullWith src={detailHistory[`${item}_url`]} alt={item} />
              </Grid>
            ) : (
              ""
            )
          )}
        </Grid>
      </Grid>
    </Box>
  );
}

export default withRouter(KYCHistory);
